@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@400;500&display=swap');

*{
  padding: 0;
  margin: 0;
  background-color: #d2dbdd;
}

.heading_style {
  padding: 30px 0;
  text-align: center;
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
  font-size: 1.5rem;
  margin-bottom: 30px;
}

.cards {
  width: 100%;
  height: auto;
}

.card {
  margin: 6%;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  background-color: #fff;
  width: 21.25%;
  border-radius: 12px;
  box-shadow: 0 13px 10px -7px rgba(0, 0, 0, 0.1);
  float: left;
}

.card:hover {
  box-shadow: 0 30px 18px -8px;
  transform: scale(1.05, 1.05);
}

.card_img {
  width: 100%;
  height: 400px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.card_info {
  background-color: #fff;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  padding: 16px 20px 20px 24px;
  border-radius: 12px;
}

.card_category {
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;

}

.card_title {
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: "Raleway", sans-serif;
  background-color: #fff;
  text-transform: capitalize;
}

button {
  padding: 5px 8px;
  font-family: "Raleway", sans-serif;
  text-transform: capitalize;
  letter-spacing: 2px;
  font-weight: 500;
  font-size: 13px;
  color:#525252;
  background-color: rgb(240, 240, 240);
  outline: none;
  border: 1px solid black;
  cursor: pointer;
}

button:hover {
  background-color: red;
}

h1:hover {
  text-transform: capitalize;
}


.footer {
  position: absolute;
  bottom: -110%;
  margin-left: 46%;
}

.vm {
  font-style: italic;
}

h4 {
  text-decoration: dashed;
  font-weight: 100;
}

@media (max-width: 768px) {

.card {
  width: 80%;
  margin: 10%;
  float: none;
}
 
.footer {
  position: absolute;
  text-align: center;
  bottom: -300%;
  margin-left: 25%;
}

}